import { createSlice } from '@reduxjs/toolkit'

let shoppingBasket = createSlice({
    name : 'shoppingBasket',
    initialState : [{id:0, name:'White and Black', count:2},{id:2, name:'Grey Yordan', count:1}],
    reducers : {
        changeCount(state, action){
            let find = state.find(function(x){
                return x.id == action.payload
            })    
     
            find.count += 1 
        },
        addCart(state, action){
            let find = state.find(function(x){
                return x.id == action.payload.id
            }) 
            console.log(action.payload.title)
            if(find != null) find.count += 1 
            
            else{
                state.unshift({ id : action.payload.id, name : action.payload.title , count : 1})
            }

        }
    }
})

export let { changeCount, addCart } = shoppingBasket.actions

export default shoppingBasket