
import './App.css';

import 'bootstrap/dist/css/bootstrap.min.css'; 
import {Button, Navbar, Container, Nav} from 'react-bootstrap'

import {lazy, Suspense, useEffect, useState} from 'react'
import axios from 'axios';
import {Route, Routes, Link, useNavigate, Outlet} from 'react-router-dom'

import BeatLoader from 'react-spinners/BeatLoader'; 
 
import data from './data.js';

//ProductDetail 컴포넌트가 필요해지면 import 해주세요
//첫 페이지 로딩속도를 향상 
//lazy, Suspense
const ProductDetail = lazy( () => import('./routes/ProductDetail.js') );
const Cart = lazy( () => import('./routes/Cart.js') );
const ExUseEffect = lazy( () => import('./routes/ExUseEffect.js') );
const ExData = lazy( () => import('./routes/ExData.js') );
const Blog = lazy( () => import('./routes/Blog.js') );
const ExReactQuery = lazy( () => import('./routes/ExReactQuery.js') );
const ExImage = lazy( () => import('./routes/ExImage.js') );
const ExMemo = lazy( () => import('./routes/ExMemo.js') );
const ExUseTransition = lazy( () => import('./routes/ExUseTransition.js'));


function App() {
  let watched = JSON.parse(localStorage.getItem('watched'))

  let [shoes, setShoes] = useState(data)
  let navigate = useNavigate()
  let [morePageNum, setMorePageNum] = useState(2)
  let [loading, setLoading] = useState(false);



  
  useEffect(()=>{
    //최근본 상품s
    if(watched == null)  localStorage.setItem('watched', JSON.stringify([])) 
    
  },[])

  //loading
  const loadingCss = {
    display: "block",
    position: "absolute",
    align : "center",  
  };
  
  return (
    <div className="App">
      <div>
      
      <Navbar bg="dark" variant="dark">
        <Container>
          <Navbar.Brand href="/">Home</Navbar.Brand>
          <Nav className="me-auto">
            <Nav.Link onClick={()=>{ navigate('/detail/1')}}>Detail</Nav.Link>
            <Nav.Link onClick={()=>{ navigate('/about/member')}}>About</Nav.Link>
            <Nav.Link onClick={()=>{ navigate('/exUseEffect')}}>ExUseEffect</Nav.Link>
            <Nav.Link onClick={()=>{ navigate('/exData')}}>ExData</Nav.Link>
            <Nav.Link onClick={()=>{ navigate('/cart')}}>Cart</Nav.Link>
            <Nav.Link onClick={()=>{ navigate('/blog')}}>Blog</Nav.Link>
            <Nav.Link onClick={()=>{ navigate('/exReactQuery')}}>ExReactQuery</Nav.Link>
            <Nav.Link onClick={()=>{ navigate('/exImage')}}>ExImage</Nav.Link>
            <Nav.Link onClick={()=>{ navigate('/exMemo')}}>ExMemo</Nav.Link>
            <Nav.Link onClick={()=>{ navigate('/exUseTransition')}}>ExUseTransition</Nav.Link>
          </Nav>
        </Container>
      </Navbar>
      <Suspense fallback={ <div>로딩중임</div> }>
      <Routes>
        <Route path='/' element={
          <>
          <div className='main-bg'> 
          </div>
          <div>최근본 상품
            {
              watched !== null && watched.map((procut , i) => {
                return procut
              })  
            }
            <div>
          
    </div>
          </div>
          <div className="container">
            <div>
              <BeatLoader loading={ loading } 
              size={30}
              cssOverride={loadingCss} color="#bf8df2" height="10px"
              aria-label="Loading Spinner"
              data-testid="loader"
              width="250px"/>
            </div>
          {/* 메인-상품전시 */}
          <div className="row">
            {
              shoes.map((product, index) => (
                <ProductDisplay id={product.id} title={product.title} price={product.price} image={product.id+1}/>
              ))  
            }
            
          </div>
        </div> 
        {
        //버튼 누른 횟수를 저장해서 2번째 데이퍼 가져오고 3번째는 데이터업음
        morePageNum<4 &&<button onClick={()=>{
          //로딩중UI  뛰우기
          setLoading(true)
          axios.get("https://codingapple1.github.io/shop/data"+morePageNum+".json")
          .then((result)=>{
            console.log(result.data[0])
            let copy = [...shoes, ...result.data]
            //copy.unshift(...result.data)
            setShoes(copy)
            setMorePageNum(morePageNum+1)
            console.log(morePageNum)
            //로딩중UI 숨기기
            setLoading(false) 
          })
          .catch(()=>{
            //로딩중UI 숨기기
            setLoading(false) 
            console.log("fail")
          })
          

          //axios.post("url1",{ name : 'kim'})

          Promise.all([axios.get("url1"),axios.get("url2")])
          .then(()=>{
            //axios url1,url2 둘다 성공햇을경우 실행행
          }).catch(()=>{
            console.log("Promise all fail")
          })
        
        }}>더보기</button>
        }
        </>
        }></Route>
         
        <Route path='/detail/:id' element={ <ProductDetail shoes={ shoes }></ProductDetail>} />
        <Route path="/about" element={ <About></About>}>
          <Route path='member' element={ <div>member</div>}></Route>
          <Route path='location' element={ <div>location</div>}></Route> 
        </Route>
        <Route path="/event" element={ <Event></Event> }>
          <Route path='one' element={ <div>one</div> }></Route>
          <Route path='two' element={ <div>two</div> }></Route> 
        </Route>
        <Route path='/exUseEffect' element={ <ExUseEffect></ExUseEffect> }></Route>
        <Route path='/exData' element={ <ExData></ExData> }></Route>
        <Route path="*" element={ <div>페이지를 찾을 수 없습니다.</div>}></Route>
        <Route path='/cart' element={ <Cart></Cart> }></Route>
        <Route path='/blog' element={ <Blog></Blog> }></Route>
        <Route path='/exReactQuery' element={ <ExReactQuery></ExReactQuery> }></Route>
        <Route path='/exImage' element={ <ExImage></ExImage> }></Route>
        <Route path='/exMemo' element={ <ExMemo></ExMemo> }></Route>
        <Route path='/exUseTransition' element={ <ExUseTransition></ExUseTransition> }></Route>
      </Routes>
      </Suspense>
        
      </div>
      
      
    </div>
  );
}
// 메인-상품전시
function ProductDisplay(props){
  return(
    <div className="col-md-4">
      <Link to={"/detail/"+props.id}>
      <img src={ "https://codingapple1.github.io/shop/shoes"+props.image+".jpg"} width="80%" />
      </Link>
      <h4>{ props.title }</h4>
      <p>{ props.price }</p>
    </div>  
  )
}

function About(){
  return(
    <div>
      <h4>About</h4>
      <Outlet></Outlet>
    </div>
  )
}

function Event(){
  return(
    <div>
      <h4>Event</h4>
      <Outlet></Outlet>
    </div>
  )
}


export default App;
